.profile {
	margin-bottom: 40px;
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__header-person {
	}

	&__header-action {
	}

	&__body {
	}

	&__socio {
	}

	&__text {
		font-weight: 500;
		font-size: 14px;
		line-height: 18px;
		max-width: 1060px;
		span {
			color: $gray_text_color;
		}
	}
	.action {
		display: flex;
		align-items: center;
		&__follow {
			display: flex;
			align-items: center;
		}

		&__follow-item {
			margin-right: 16px;
			h6 {
				font-weight: 500;
				font-size: 14px;
				line-height: 18px;
				color: $white_color;
			}
			p {
				font-weight: 400;
				font-size: 14px;
				line-height: 18px;
				color: $gray_text_color;
			}
		}

		&__option {
		}
		.button {
			&-body {
				padding: 13px 30px;
			}
		}
	}

	.socio {
		display: flex;
		align-items: center;
		margin: 25px 0 16px 0;
		&__item {
			display: flex;
			align-items: center;
			margin-right: 26px;
			&:last-child {
				margin-right: 0;
			}
			img {
				margin-right: 8px;
			}
			span {
			}
		}
	}

	@media (max-width: 768px) {
		&__header {
			align-items: flex-start;
			flex-direction: column;
		}
		.person {
			margin-bottom: 10px;
		}
		.action {
			width: 100%;
			align-items: center;
			flex-direction: column;
			&__follow {
				margin-bottom: 10px;
			}
		}
	}
	@media (max-width: 500px) {
		.socio {
			flex-wrap: wrap;

			&__item {
				margin: 0 0 5px 0;
				flex: 1 0 50%;
			}
		}
	}
}
